import React, { useState } from 'react';
import { Card, CardImg, CardBody, CardTitle, CardText, Breadcrumb, BreadcrumbItem, Modal, ModalHeader, Button, ModalBody, Label, Col, Row } from 'reactstrap';
import { Link } from 'react-router-dom';
import Moment from "react-moment";
import { Control, Errors, LocalForm } from 'react-redux-form';
import { Loading } from './Loadingcomponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components'

const required = (val) => val && val.length

function RenderDish({ dish }) {
    if (dish == null) {
        return (<div></div>);
    }
    return (
        <div className='col-12 col-md-6 p5'>
            <FadeTransform in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }} >
                <Card>
                    <CardImg width='100%' src={baseUrl + dish.image} alt={dish.name} />
                    <CardBody>
                        <CardTitle tag='h4'>{dish.name}</CardTitle>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>
        </div>
    );
}

function RenderComments({ comments, postComment, dishId }) {
    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);

    if (comments == null) {
        return (<div></div>);
    }
    const showComment = comments.map((comment) => {
        return (
            <Fade in>
            <div key={comment.id}>
                <p><b>{comment.comment}</b> <br />
                    <i>-- {comment.author} {"-- "}
                        <Moment format="MMM DD,YYYY">{comment.date}</Moment>
                    </i>
                </p>
            </div>
            </Fade>

        );
    });

    const handleSubmit = (values) => {
        postComment(dishId, values.rating, values.author, values.comment);
    }
    return (
        <div className='col-12 col-md-5 m-1'>
            <h2 className="text-primary">Comments</h2>
            <hr />
            <Stagger in >
                {showComment}
            </Stagger>
            <Modal isOpen={modal} toggle={toggle}>
                <ModalHeader toggle={toggle}>Add new comment</ModalHeader>
                <ModalBody>
                    <LocalForm onSubmit={(values) => handleSubmit(values)}>
                        <Row className='form-group'>
                            <Label htmlFor='rating' md={3}>Rating</Label>
                            <Col md={9}>
                                <Control.select
                                    model='.rating'
                                    name='rating'
                                    id='rating'
                                    className='form-control'
                                    validators={{
                                        required
                                    }}>

                                    <option value='1'>1</option>
                                    <option value='2'>2</option>
                                    <option value='3'>3</option>
                                    <option value='4'>4</option>
                                    <option value='5'>5</option>
                                </Control.select>
                            </Col>
                            <Errors
                                model='.rating'
                                className='text-danger'
                                show='touched'
                                messages={{
                                    required: 'Không rate à!'
                                }}
                            />


                        </Row>
                        <Row className='form-group'>
                            <Label htmlFor='comment' md={3}>Comment</Label>
                            <Col md={9}>
                                <Control.textarea
                                    model='.comment'
                                    name='comment'
                                    id='comment'
                                    className='form-control'
                                    placeholder='Content comment here...'
                                    rows='4'
                                    validators={{
                                        required
                                    }}
                                />
                            </Col>
                            <Errors
                                model='.comment'
                                className='text-danger'
                                show='touched'
                                messages={{
                                    required: 'Không được bỏ trống!'
                                }}
                            />
                        </Row>

                        <Row className='form-group'>
                            <Label htmlFor='author' md={3}>Author</Label>
                            <Col md={9}>
                                <Control.text
                                    model='.author'
                                    name='author'
                                    id='author'
                                    className='form-control'
                                    validators={{
                                        required
                                    }}
                                />
                            </Col>
                            <Errors
                                model='.author'
                                show='touched'
                                className='text-danger'
                                messages={{
                                    required: 'Không được bỏ trống!'
                                }}
                            />
                        </Row>
                        <Row className='form-group'>
                            <Col md={{ size: 9, offset: 3 }}>
                                <Button type='submit'>Add</Button>
                            </Col>

                        </Row>
                    </LocalForm>
                </ModalBody>
            </Modal>
            <Button color='primary' onClick={toggle}>Add comment</Button>
        </div>
    );
}

const DishDetail = (props) => {


    if (props.isLoading) {
        return (
            <div className='container'>
                <div className='row'>
                    <Loading />
                </div>
            </div>
        );
    } else if (props.errMess) {
        return (
            <div className='container'>
                <div className='row'>
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        );
    } else if (props.dish != null) {
        const dish = props.dish;
        const comments = props.comments;

        const dishItem = <RenderDish dish={dish} />;
        const dishComments = <RenderComments comments={comments} postComment={props.postComment} dishId={dish.id} />;
        return (
            <div className='container'>
                <div className="row">
                    <Breadcrumb className='col-md-12'>

                        <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{dish.name}</h3>
                        <hr />
                    </div>

                </div>

                <div className='row'>
                    {dishItem}
                    {dishComments}
                </div>
            </div>

        )
    } else {
        return (
            <div></div>
        )
    };
}

export default DishDetail;